@import url(https://fonts.googleapis.com/css?family=Roboto);
html, body, #root {
  min-height: 100vh; }

body {
  margin: 0;
  overflow: hidden; }

.line {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 2px;
  background: #345B6B;
  width: 100px; }

.point {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0; }

.chapter {
  -webkit-transform: translate(-50%, -45%);
          transform: translate(-50%, -45%);
  opacity: 0;
  -webkit-transition-duration: 1s;
          transition-duration: 1s; }

.appear .chapter {
  -webkit-transition-delay: 0.6s;
          transition-delay: 0.6s;
  opacity: 1;
  -webkit-transform: translate(-50%, -60%);
          transform: translate(-50%, -60%); }

.presentation {
  background: -webkit-radial-gradient(47.18% -0.59%, 724.28px, #202020 0%, #414C57 0.01%, #070F17 100%);
  background: radial-gradient(724.28px at 47.18% -0.59%, #202020 0%, #414C57 0.01%, #070F17 100%); }

.presentation {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute; }
  .presentation__background {
    pointer-events: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    user-drag: none;
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-transition-duration: 2s;
            transition-duration: 2s; }

.Intro__title {
  opacity: 0;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: 40px; }
  .Intro__title__part1 {
    -webkit-transform: translate(-80%, -100%);
            transform: translate(-80%, -100%); }
  .Intro__title__part2 {
    -webkit-transition-delay: 0.1s;
            transition-delay: 0.1s;
    -webkit-transform: translate(-26.5%, -85%);
            transform: translate(-26.5%, -85%); }

.Intro__table {
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 790px; }
  .Intro__table > img {
    margin-top: 41px;
    opacity: 1;
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s; }
    .Intro__table > img.hide {
      opacity: 0; }

.Intro .line {
  width: 0px;
  -webkit-transform: none;
          transform: none;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s; }
  .Intro .line.right {
    top: -40px;
    left: 120px; }
  .Intro .line.left {
    top: 55px;
    left: -370px; }

.Intro.appear .Intro__title {
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
  opacity: 1;
  margin-top: 0; }

.Intro.appear .line {
  -webkit-transition-delay: 0.8s;
          transition-delay: 0.8s;
  width: 160px; }
  .Intro.appear .line.left {
    -webkit-transition-delay: 0.9s;
            transition-delay: 0.9s; }

.Intro > .point {
  -webkit-transition-duration: 2s;
          transition-duration: 2s; }

.Intro > .point:nth-child(2) {
  -webkit-transform: translate(0, 1000px) scale(1.8);
          transform: translate(0, 1000px) scale(1.8);
  opacity: 0;
  -webkit-filter: blur(5px);
          filter: blur(5px); }

.Intro.showTable > .point:nth-child(1) {
  -webkit-transform: translate(0, -400px) scale(0.85);
          transform: translate(0, -400px) scale(0.85);
  -webkit-filter: blur(5px);
          filter: blur(5px);
  opacity: 0.2; }

.Intro.showTable > .point:nth-child(2) {
  -webkit-transform: none;
          transform: none;
  opacity: 1;
  -webkit-filter: none;
          filter: none; }

.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  -webkit-transition-duration: 1s;
          transition-duration: 1s; }
  .slide.current {
    opacity: 1; }
  .slide > div, .slide > section, .slide > article {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0; }

